import { createContext } from "react";

export const ProjectsContext = createContext({});
/* 

    1 - Project Context yaps oluturulmal
    2 - ProjectContext.Provider yaps App.js dosyasnda child componenetleri kapsamal 
    3 - App.js dosyasnda ilk parametre deiken ikinci parametre method
        const [widgetValue, setWidgetValue] = useState([]) 
    4 - Child componentlerde stateleri kullanmak iin, hangi child componentse onun iinde useContext yaps oluturulmal 
        const { setScreenSize, screenSize } = useContext(ProjectsContext); 
    5 - *Button onclick = clickHandler* gibi bir yap ile state gncellemesi yaplr.
        const handleClick = e => {

            setWidgetValue(e);

            console.log(e.target.innerText);

            localStorage.setItem("widgetValue", e.target.innerText)
            
            }

 */